import React, { useCallback, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { authActions } from "../store/auth";
import classes from "./CreateCoupon.module.css";

export default function CreateCustomer() {
    const token = useSelector((state) => state.auth.token);
    const dispatch = useDispatch();
    const firstNameRef = useRef("");
    const lastNameRef = useRef("");
    const emailRef = useRef("");
    const passwordRef = useRef("");

const addCustomerHandler = useCallback(async (event) => {
    event.preventDefault();
    const customer = {
      firstname: firstNameRef.current.value,
      lastname: lastNameRef.current.value,
      email: emailRef.current.value,
      password: passwordRef.current.value,
    };
    console.log(customer);
    const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json", token: token },
        body: JSON.stringify(customer),
      };
      try {
        const response = await fetch("/admin/addCustomer", requestOptions);
        if (!response.ok) {
          window.alert("Session timeout!");
          dispatch(authActions.logout());
          throw new Error("Something went wrong!");
        }
        
      const responseMsg = await response.text();
      alert(responseMsg);
    } catch (error) {
      console.log(error.message);
    }
  }, []);

  return (
 <form onSubmit={addCustomerHandler}>
 <div className={classes.control}>
   <label htmlFor="customer-firstname">Customer first name:</label>
   <input type="text" id="customer-name" ref={firstNameRef} />
 </div>
 <div className={classes.control}>
   <label htmlFor="customer-lastname">Customer last name:</label>
   <input type="text" id="customer-lastname" ref={lastNameRef} />
 </div>
 <div className={classes.control}>
   <label htmlFor="customer-email">Customer email:</label>
   <input type="text" id="customer-email" ref={emailRef} />
 </div>
 <div className={classes.control}>
   <label htmlFor="customer-password">Customer password:</label>
   <input type="password" id="customer-password" ref={passwordRef}  minLength="3" required />
 </div>
 <button>Add Customer</button>
</form> );
}