import { useDispatch, useSelector } from "react-redux";

import classes from "./Auth.module.css";
import { authActions } from "../store/auth";
import { itemsActions } from "../store/items";
import React, { useCallback, useRef, useState } from "react";

import Select from "react-select";

const Auth = () => {
  const emailRef = useRef("");
  const passwordRef = useRef("");
  const roleRef = useRef("");
  const [selectedRole, setSelectedRole] = useState(null);

  const dispatch = useDispatch();
  const token = useSelector((state) => state.auth.token);

  const options = [
    { value: "admin", label: "Admin" },
    { value: "company", label: "Company" },
    { value: "customer", label: "Customer" },
  ];

  const loginHandler = useCallback(async (event) => {
    try {
      event.preventDefault();

      const creds = {
        email: emailRef.current.value,
        password: passwordRef.current.value,
        role: selectedRole
      };

      console.log("post: "+JSON.stringify(creds));

      // POST request using fetch inside useEffect React hook
      const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(creds),
      };

      const response = await fetch("/login", requestOptions);
      if (!response.ok) {
        window.alert("Please check your credentials!");
        dispatch(authActions.logout());
        console.log("body: " + JSON.stringify(response));
        throw new Error("Authentication fails!");
      }
      const token = await response.text();
      
      const payload = 
      {
        token: token,
        role: creds.role
      }
      dispatch(authActions.login(payload));
    } catch (error) {
      console.log(error);
    }
  }, [dispatch, selectedRole]);

  const roleChangeHandler = e => {
    setSelectedRole(e.value);
  }

  return (
<React.Fragment>

<main className={classes.auth}>
  <section>
    <form onSubmit={loginHandler}>
      <div className={classes.control}>
        <label htmlFor="email">Email</label>
        <input type="email" id="email" ref={emailRef} />
      </div>
      <div className={classes.control}>
        <label htmlFor="password">Password</label>
        <input type="password" id="password" ref={passwordRef} />
      </div>
      <div className={classes.control}>
        <label htmlFor="select">Role</label>
        <Select
          id="select"
          value={options.find(obj => obj.value === selectedRole)}
          options={options}
          onChange={roleChangeHandler} 
        />
      </div>
      <button type="submit">Login</button>
    </form>
  </section>
</main>

<main className={classes.auth}>
  <section>
    <p>The site is composed of a default main page that includes links that routes to several locations.

The first authentication to the system will be as an Administrator.
We will send an email with the password attached for the first authentication.

After the login, the website will route you to the admin personal area, the user will be routed to the personal area of the Admin, there he will have a menu with the functions he can use such as: creating companies, updating said companies, etc.

In case you logged in as a company, the user will be routed to the personal area of the company, there you will find a menu with a myriad of functions to use, such as creating new coupons.

In case you logged in as a customer, the user will be routed to the customer menu.
Here, you can purchase coupons from any category.

Each of the users can go back to their personal area by pressing the client type button. We hope you enjoy our product!</p>
   <img src="https://lh3.googleusercontent.com/Ex8HwVS6YiuQ4TIMLH9w36imROv2tnRexdoSNFU_nCSwEjDrgcWTq-q3VTy2pG_0iGCDmg=s115"/>
  </section>
</main>

</React.Fragment>
  );
};

export default Auth;
