import React, { useCallback, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { authActions } from "../store/auth";
import classes from "./CreateCoupon.module.css";

export default function CreateCompany() {
    const token = useSelector((state) => state.auth.token);
    const dispatch = useDispatch();
    const nameRef = useRef("");
    const emailRef = useRef("");
    const passwordRef = useRef("");

const addCompanyHandler = useCallback(async (event) => {
    event.preventDefault();
    const company = {
      name: nameRef.current.value,
      email: emailRef.current.value,
      password: passwordRef.current.value,
    };
    console.log(company);
    const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json", token: token },
        body: JSON.stringify(company),
      };
      try {
        const response = await fetch("/admin/addCompany", requestOptions);
        if (!response.ok) {
          window.alert("Session timeout!");
          dispatch(authActions.logout());
          throw new Error("Something went wrong!");
        }
        
      const responseMsg = await response.text();
      alert(responseMsg);
    } catch (error) {
      console.log(error.message);
    }
  }, []);

  return (
 <form onSubmit={addCompanyHandler}>
 <div className={classes.control}>
   <label htmlFor="company-name">Company Name:</label>
   <input type="text" id="company-name" ref={nameRef} />
 </div>
 <div className={classes.control}>
   <label htmlFor="company-email">Company email:</label>
   <input type="text" id="company-email" ref={emailRef} />
 </div>
 <div className={classes.control}>
   <label htmlFor="company-password">Company password:</label>
   <input type="password" id="company-password" ref={passwordRef}  minLength="3" required />
 </div>
 <button>Add Company</button>
</form> );
}