import React, { useCallback, useRef } from "react";
import ReactDOM from "react-dom";
import Popup from "reactjs-popup";

import classes from "./CreateCoupon.module.css";
import { useDispatch, useSelector } from "react-redux";
import { authActions } from "../store/auth";

function CreateCoupon(props) {
  const token = useSelector((state) => state.auth.token);
  const dispatch = useDispatch();

  const amountRef = useRef("");
  const descRef = useRef("");
  const categoryRef = useRef("");
  const titleRef = useRef("");
  const startDateRef = useRef("");
  const endDateRef = useRef("");
  const priceRef = useRef("");
  const imgRef = useRef("");



  const addCouponHandler = useCallback(async (event) => {
    event.preventDefault();

    const coupon = {
      amount: amountRef.current.value,
      description: descRef.current.value,
      category: categoryRef.current.value,
      title: titleRef.current.value,
      startDate: startDateRef.current.value,
      endDate: endDateRef.current.value,
      price: priceRef.current.value,
      image: imgRef.current.value
    };
    
    console.log(JSON.stringify(coupon));

    // POST request using fetch inside useEffect React hook
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json", token: token },
      body: JSON.stringify(coupon),
    };
    try {
      const response = await fetch("/company/addCoupon", requestOptions);
      if (!response.ok) {
        // window.alert(response.text);
        window.alert("Session timeout!");
        dispatch(authActions.logout());
        throw new Error("Something went wrong!");
      }

      console.log("Response Okay!");
      const id = await response.text();
      console.log("got number: "+id);
      console.log("coupon sent to server: " + coupon);
      // id: 0
      // title":"Coupon_49","
      // details":"This is a discription for coupon 49","
      // date":"Sun Jan 02 21:45:14 IST 2022"}
      const couponWithId =  {"id":id,...coupon};
      console.log("props.onAddCoupon: " + couponWithId);
      props.onAddCoupon(couponWithId);
    } catch (error) {
      console.log(error.message);
    }
    //setIsLoading(false);
  }, []);

  return (
    <form onSubmit={addCouponHandler}>
      <div className={classes.control}>

        <label htmlFor="title">Title</label>
        <input type="text" id="title" ref={titleRef} />

        <label htmlFor="amount">Amount</label>
        <input type="number" id="amount" ref={amountRef} />

        <label htmlFor="price">Price</label>
        <input type="number" id="price" ref={priceRef} />

        <label>Description</label>
        <textarea rows="5" id="desc" ref={descRef}></textarea>

        <label htmlFor="sdate">Release Date</label>
        <input type="date" id="sdate" ref={startDateRef}/>

        <label htmlFor="edate">Expiration Date</label>
        <input type="date" id="edate" ref={endDateRef} />
        
        <label htmlFor="img">Image link</label>
        <input type="text" id="title" ref={imgRef} />

        <label htmlFor="category">Category</label>
        <select ref={categoryRef}>
        <option value="RESTAURANT">Restaurant</option>
        <option value="ELECTRICITY">Electricity</option>
        <option value="SHOPPING">Shopping</option>
        <option value="VACATION">Vacation</option>
        </select>
      </div>
      <button>Add Coupon</button>
    </form>
  );
}

// {ReactDOM.createPortal(<Popup show={showPopup} popuptext="wrong movie details!"/>
// , document.getElementById("popup-place-holder"))}

export default CreateCoupon;
