import React from 'react';
import { useDispatch, useSelector } from "react-redux";
import classes from './Movie.module.css';
import { useCallback, useRef, useEffect } from "react";
import { authActions } from "../store/auth";

const ShowDetails = (props) => {

  console.log(props.item);

  const buyButton = props.buyable;
  const token = useSelector((state) => state.auth.token);
  const dispatch = useDispatch();
  let type = props.type;
  let id = props.item.id;


  const deleteItemHandler = useCallback(async (event) => {
    const requestOptions = {
        method: "DELETE",
        headers: { "Content-Type": "application/json", token: token },
      };
      try {
        let fetchString = null;
        if(type==="Coupon")
          fetchString = "company/deleteCoupon/"+id
        if(type==="Company" || "Customer")
          fetchString = "admin/delete"+type+"/"+id

        const response = await fetch(fetchString, requestOptions);
        if (!response.ok) {
          window.alert("Session timeout!");
          dispatch(authActions.logout());
          throw new Error("Something went wrong!");
        }
        
      const responseMsg = await response.text();
      alert(responseMsg);
    } catch (error) {
      console.log(error.message);
    }
  }, []);

  const purchaseCouponHandler = useCallback(async (event) => {
    const requestOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json", token: token },
        body: JSON.stringify(props.item)
      };
      try {

        const response = await fetch("/customer/purchaseCoupon", requestOptions);
        if (!response.ok) {
          window.alert("Session timeout!");
          dispatch(authActions.logout());
          throw new Error("Something went wrong!");
        }
        
      const responseMsg = await response.text();
      alert(responseMsg);
    } catch (error) {
      console.log(error.message);
    }
  }, []);

  const CompanyDetails = () => {
    return(
      <React.Fragment>
        <span>{props.item.name}</span>
      </React.Fragment>
    );
  }

  const CustomerDetails = () => {
    return(
      <React.Fragment>
        <span>{props.item.firstname} </span>
        <span>{props.item.lastname}</span>
      </React.Fragment>
    );
  }

  const CouponDetails = () => {
    return(
      <React.Fragment>
        <span>{props.item.title} </span>
        <span>{props.item.description} </span>
        <span>{props.item.startDate} </span>
        <span>{props.item.endDate} </span>
        <span>{props.item.amount} </span>
        <span>{props.item.price} </span>
      </React.Fragment>
    );
  }

  return (
    <div className={classes.movie}>

      {type==="Company" && CompanyDetails()}
      {type==="Customer" && CustomerDetails()}
      {type==="Coupon" && CouponDetails()}
      <button id='deleteButton' onClick={buyButton==="true" ? purchaseCouponHandler : deleteItemHandler}>{buyButton==="true"?"Buy":"Delete"}</button>
    </div>
  );
};

export default ShowDetails;
