import React, { useState, useEffect, useCallback, useRef } from "react";

import MoviesList from "./MoviesList";
import CreateCoupon from "./CreateCoupon";
import './Main.module.css';
import { useDispatch, useSelector } from "react-redux";
import { authActions } from "../store/auth";
import ShowDetails from "./ShowDetails";

function Main() {
  const token = useSelector(state => state.auth.token);
  const loggedInName = useSelector(state => state.auth.token);
  const dispatch = useDispatch();
  const [coupons, setCoupons] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const filterByRef = useRef("");
  const maxPriceRef = useRef("");
  const categoryRef = useRef("");
  const [filter, setFilter] = useState("");
  const [content, setContent] = useState(<p>Nothing to show...</p>);

  const fetchDetailsHandler = useCallback(async () => {
    setIsLoading(true);
    setError(null);

    const requestOptions = {
      method: "GET",
      headers: { "Content-Type": "application/json" , token: token},
    };

    try {
      
      const response = await fetch("/company/getDetails", requestOptions);
      if (!response.ok) {
        window.alert("Session expired");
        dispatch(authActions.logout());
        throw new Error("Something went wrong!");
      }
      
      const data = await response.json();
      
      dispatch(authActions.setName(data.name))
 
    } catch (error) {
      setError(error.message);
    }
    setIsLoading(false);
  }, []);

  useEffect(() => {
    fetchDetailsHandler();
  }, [fetchDetailsHandler]);

  const fetchCouponsHandler = useCallback(async () => {
    setIsLoading(true);
    setError(null);

    const requestOptions = {
      method: "GET",
      headers: { "Content-Type": "application/json" , token: token},
    };

    try {
      
      let fetchString = ""
      if(filterByRef.current.value ==="ALL"){
        fetchString = "/company/getCompanyCoupons"
      }
      if(filterByRef.current.value ==="category"){
        fetchString = "/company/getCompanyCouponsByCategory/"+categoryRef.current.value;
      }
      if(filterByRef.current.value ==="maxPrice"){
        fetchString = "/company/getCompanyCouponsByMaxPrice/"+maxPriceRef.current.value;
      }

      console.log(fetchString);

      const response = await fetch(fetchString, requestOptions);
      if (!response.ok) {
        window.alert("Session expired");
        dispatch(authActions.logout());
        throw new Error("Something went wrong!");
      }
      
      const data = await response.json();
      
      setContent(data.map(coupon => <ShowDetails type="Coupon" item={coupon}/>));
 
    } catch (error) {
      setError(error.message);
    }
    setIsLoading(false);
  }, []);

  const categoryHtml = () => {
    return(
      <select ref={categoryRef}>
        <option value="RESTAURANT">Restaurant</option>
        <option value="ELECTRICITY">Electricity</option>
        <option value="SHOPPING">Shopping</option>
        <option value="VACATION">Vacation</option>
      </select>
    );
  }

  function addCouponHandler(coupon) {
    console.log(coupon);
  }

  const test = () => {
    console.log("test filter by is now: " + filterByRef.current.value);
    if(filterByRef.current.value==="category"){
      setFilter(categoryHtml())
    }
    if(filterByRef.current.value==="maxPrice"){
      setFilter(<input type="number" ref={maxPriceRef}/>)
    }
    if(filterByRef.current.value==="ALL"){
      setFilter("");
    }
  }

  return (
    <React.Fragment>
      <section>
        <CreateCoupon onAddCoupon={addCouponHandler} />
      </section>
      <section>
      <button onClick={fetchCouponsHandler}>Get coupons</button>
      <select ref={filterByRef} onChange={test}>
        <option value="ALL">No filter</option>
        <option value="category">Category</option>
        <option value="maxPrice">Max price</option>
      </select>
        {filter}
        {content}
        </section>
      
    </React.Fragment>
  );
}

export default Main;
